import { HtmlHTMLAttributes, InputHTMLAttributes, useId ,ReactNode } from "react";
import "../style/design.css";

export function TextInput({
  input,
  label,
  inputRef,
  labelRef,
  icon,
  position = "start", // By default, icon will be at the start
}: {
  inputRef?: React.RefObject<HTMLInputElement>;
  labelRef?: React.RefObject<HTMLLabelElement>;
  input: InputHTMLAttributes<HTMLInputElement>;
  label: HtmlHTMLAttributes<HTMLLabelElement>;
  icon?: ReactNode; // Optional icon element (ReactNode allows for any valid JSX)
  position?: "start" | "end"; // Position for the icon
}) {
  const id = useId();

  return (
    <div
      className="text-input"
      style={{
        display: "flex",
        alignItems: "center",
      }}
    >
      <label {...label} htmlFor={id} ref={labelRef}>
        {label.title}
      </label>
      <div
        style={{
          display: "flex",
          alignItems: "center",
          height: "18px",
          width:"100%"
        }}
        className="input-container"

      >
        {position === "start" && icon && (
          <span style={{ marginRight: "4px" }}>{icon}</span>
        )}
        <input
          ref={inputRef}
          id={id}
          {...input}
          style={{
            height: "18px",
            ...input.style,
          }}
        />
        {position === "end" && icon && (
          <span style={{ marginLeft: "4px" }}>{icon}</span>
        )}
      </div>
    </div>
  );
}

export function SelectInput({
  select,
  label,
  selectRef,
  datasource = [],
  values = "",
  display = "",
}: {
  selectRef?: React.RefObject<HTMLSelectElement>;
  labelRef?: React.RefObject<HTMLLabelElement>;
  select: InputHTMLAttributes<HTMLSelectElement>;
  label: HtmlHTMLAttributes<HTMLLabelElement>;
  datasource: Array<any>;
  values: string;
  display: string;
}) {
  const id = useId();
  return (
    <div
      style={{
        display: "flex",
        height: "18px",
        alignItems: "center",
      }}
    >
      <label {...label} htmlFor={id}>
        {label.title}
      </label>
      <select
        {...select}
        ref={selectRef}
        className="select"
        style={{
          height: "18px",
          ...select.style,
        }}
      >
        {datasource.map((itm, idx) => {
          return (
            <option key={idx} value={itm[values]}>
              {itm[display]}
            </option>
          );
        })}
      </select>
    </div>
  );
}

export function ButtonField({
  buttonRetRef,
  button,
  tooltipText = "",
  children,
  disabled = false,
}: {
  buttonRetRef?: React.RefObject<HTMLButtonElement>;
  button: HtmlHTMLAttributes<HTMLButtonElement>;
  tooltipText: string;
  children: React.ReactNode;
  disabled?: boolean;
}) {
  return (
    <div className="tooltip">
      <button
        disabled={disabled}
        {...button}
        ref={buttonRetRef}
        style={{ ...button.style, background:!disabled ? "transparent" : "#f1f1f1" }}
        className="tooltip-button"
      >
        {children}
      </button>
      {!disabled && <span className="tooltip-text">{tooltipText}</span>}
    </div>
  );
}
